.notifications {
  &_header {
    width: 100%;
    &_text {
      font-family: "Poppins";
      font-weight: 400;
      font-weight: 500;
      font-size: 28px;
      font-weight: 500;
      line-height: 33.6px;
      text-align: left;
      color: var(--black);
      margin-bottom: 10px;
      @media screen and (max-width: 1024px) {
        font-size: 20px;
        font-weight: 500;
        line-height: 22px;
        text-align: left;
      }
      @media screen and (max-width: 768px) {
        margin-bottom: 12px;
      }
      span {
        font-family: "Poppins";
        font-weight: 600;
        font-size: 16px;
        font-weight: 600;
        line-height: 17.6px;
        letter-spacing: 0.02em;
        text-align: left;
        @media screen and (max-width: 1024px) {
          font-size: 14px;
          font-weight: 500;
          line-height: 15.4px;
          letter-spacing: 0.02em;
          text-align: left;
        }
      }
    }
    &_subText {
      font-family: Inter;
      font-weight: 400;
      font-size: 16px;
      font-weight: 400;
      line-height: 19.2px;
      text-align: left;
      color: var(--gray);
      margin-bottom: 24px;
      @media screen and (max-width: 768px) {
        font-size: 12px;
        line-height: 14.4px;
        letter-spacing: 0.01em;
        text-align: left;
        margin-bottom: 12px;
      }
    }
    &_buttons {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 24px;
      @media screen and (max-width: 960px) {
        flex-direction: column;
        align-items: flex-start;
        gap: 12px;
      }
      &_switcher {
        width: 362px;
        height: 36px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 4px;
        background-color: var(--white);
        border-radius: 8px;
        @media screen and (max-width: 768px) {
          width: 100%;
        }
        &_item {
          cursor: pointer;
          padding: 6.5px 12px;
          border-radius: 7px;
          font-family: "Poppins";
          font-weight: 400;
          font-weight: 500;
          font-size: 14px;
          line-height: 15.4px;
          letter-spacing: 0.02em;
          text-align: center;
          background-color: var(--white);
          color: var(--gray-dark);
          @media screen and (max-width: 768px) {
            width: 33%;
            white-space: nowrap;
            padding: 6.5px 0px;
          }
          &_selected {
            color: var(--white);
            background-color: var(--gray-blue);
            @media screen and (max-width: 768px) {
              margin-left: 3px;
              margin-right: 3px;
            }
          }
        }
        &_line {
          height: 16px;
          min-width: 1px;
          background-color: var(--gray-light3);
        }
      }
      &_search {
        width: 239px;
        height: 30px;
        background-color: var(--white);
        border-radius: 7px;
        padding: 0px 10px;
        display: flex;
        align-items: center;
        @media screen and (max-width: 768px) {
          width: 100%;
          height: 40px;
        }

        img {
          margin-right: 6px;
        }
        input {
          flex: 1;
          height: 100%;
          font-family: Inter;
          font-weight: 500;
          font-size: 12px;
          font-weight: 400;
          line-height: 14.4px;
          letter-spacing: 0.01em;
          color: var(--black);
          text-align: left;
          border: none;
          outline: none;
          background-color: transparent;
          @media screen and (max-width: 768px) {
            font-size: 16px;
            line-height: 16px;
          }
        }
      }
    }
  }
  &_recentHeader {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 16px;
    &_text {
      font-family: "Poppins";
      font-weight: 400;
      font-weight: 500;
      font-size: 18px;
      font-weight: 500;
      line-height: 27px;
      text-align: left;
      @media screen and (max-width: 1024px) {
        font-family: "Poppins";
        font-weight: 600;
        font-size: 16px;
        font-weight: 600;
        line-height: 17.6px;
        letter-spacing: 0.02em;
        text-align: left;
      }
    }
    span {
      font-family: "Poppins";
      font-weight: 400;
      font-weight: 500;
      font-size: 14px;
      font-weight: 500;
      line-height: 15.4px;
      letter-spacing: 0.02em;
      text-align: center;
      color: var(--green-dark);
      cursor: pointer;
      @media screen and (max-width: 1024px) {
        font-size: 14px;
        font-weight: 500;
        line-height: 15.4px;
        letter-spacing: 0.02em;
        text-align: center;
      }
    }
  }
  &_earlierHeader {
    width: 100%;
    font-family: "Poppins";
    font-weight: 400;
    font-weight: 500;
    font-size: 18px;
    font-weight: 500;
    line-height: 27px;
    text-align: left;
    margin-bottom: 16px;
    @media screen and (max-width: 1024px) {
      font-family: "Poppins";
      font-weight: 600;
      font-size: 16px;
      font-weight: 600;
      line-height: 17.6px;
      letter-spacing: 0.02em;
      text-align: left;
    }
  }
}
.notification {
  width: 100%;
  min-height: 71px;
  background-color: var(--white);
  border-radius: 16px;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  gap: 13px;
  padding: 12px 16px;
  margin-bottom: 16px;
  position: relative;
  @media screen and (max-width: 1024px) {
    min-height: 67px;
  }
  @media screen and (max-width: 768px) {
    min-height: 61px;
    padding: 12px;
    margin-bottom: 8px;
    padding-right: 125px;
  }
  &_read {
    background-color: transparent;
    border-radius: 0px;
    border-bottom: 1px solid var(--gray-light3);
    min-height: 87px;
    margin-bottom: 0px;
    margin-bottom: 4px;
    @media screen and (max-width: 1024px) {
      min-height: 83px;
    }
    @media screen and (max-width: 640px) {
      min-height: 69px;
    }
  }
  &_header {
    display: flex;
    align-items: center;
    &_sender {
      font-family: Inter;
      font-weight: 400;
      font-size: 14px;
      line-height: 15.4px;
      text-align: right;
      color: var(--gray);
      @media screen and (max-width: 768px) {
        font-size: 10px;
        font-weight: 400;
        line-height: 14px;
        text-align: right;
      }
    }
    &_dot {
      width: 4px;
      height: 4px;
      border-radius: 50%;
      background-color: var(--gray-light3);
      margin: 4px;
    }
    &_time {
      font-family: Inter;
      font-weight: 500;
      font-size: 12px;
      font-weight: 500;
      line-height: 14.4px;
      letter-spacing: 0.01em;
      text-align: right;
      color: var(--gray-dark);
      margin-left: 6px;
      @media screen and (max-width: 768px) {
        font-size: 10px;
        font-weight: 400;
        line-height: 14px;
        text-align: right;
      }
    }
  }
  &_text {
    font-family: Inter;
    font-weight: 400;
    font-size: 16px;
    font-weight: 400;
    line-height: 19.2px;
    text-align: left;
    color: var(--black);

    @media screen and (max-width: 1024px) {
      font-size: 14px;
      font-weight: 400;
      line-height: 15.4px;
      text-align: left;
    }
    span {
      font-family: "Poppins";
      font-weight: 600;
      font-size: 16px;
      font-weight: 600;
      line-height: 17.6px;
      letter-spacing: 0.02em;
      text-align: left;
      color: var(--green-dark);

      @media screen and (max-width: 1024px) {
        font-size: 14px;
        font-weight: 600;
        line-height: 15.4px;
        letter-spacing: 0.02em;
        text-align: left;
      }
    }
  }
  &_timeAgo {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 20px;
    font-family: "Poppins";
    font-weight: 400;
    font-weight: 500;
    font-size: 14px;
    font-weight: 500;
    line-height: 15.4px;
    letter-spacing: 0.02em;
    text-align: left;
    color: var(--gray);
    @media screen and (max-width: 768px) {
      font-size: 12px;
      font-weight: 500;
      line-height: 14.4px;
      letter-spacing: 0.01em;
      text-align: left;
    }
  }
}
